
import { defineComponent } from 'vue';


export default defineComponent({
   name: 'v-select',
   props: {
      modelValue: {
         type: String,
      },
      options: {
         type: Array as () => Array<{ value: string, name: string }>,
         default: () => [],
      },
      title: {
         type: String,
         default: 'Сортировать',
      }
   }
});
